*{ margin:0; padding:0;}
html{ width:100%; height:100%; overflow:hidden;}
body{ width:100%; height:100%; font: 14px/1.5 Tahoma,Arial,Roboto,"Droid Sans","Helvetica Neue","Droid Sans Fallback","Heiti SC",sans-self;}
li{ list-style:none;}
#root , #main{ width:100%; height:100%; position:relative; margin:0 auto;}
#musicHeader{ width:100%; height: 50px; background:black; position: absolute; left: 0; top: 0; color:white; text-align: center; line-height: 50px; font-size: 18px;}
#musicHeader span{ position: absolute; left: 10px; }
#musicHeader a{ color:inherit;}
#musicList{ width: 100%; position: absolute; top:50px; bottom:65px; background:url(/images/bg.jpg); background-size: cover; color:white; overflow-y: auto; }
#musicList li{ height:60px; border-bottom: 1px #999 solid; background: rgba(0,0,0,0.5); }
#musicList li.active{ border-left:4px lightblue solid; }
#musicList .listOrder{ width:60px; float: left; font-size: 20px; line-height: 60px; text-align: center; }
#musicList .listName{ float: left; }
#musicList .listName h3{ margin-top: 10px; width:200px;white-space:nowrap;overflow:hidden; text-overflow:ellipsis}
#musicList .listName p{}
.loader {
    position: relative;
    width: 2.5em;
    height: 2.5em;
    -webkit-transform: rotate(165deg);
            transform: rotate(165deg);
    }
    .loader:before, .loader:after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    display: block;
    width: 0.5em;
    height: 0.5em;
    border-radius: 0.25em;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    }
    .loader:before {
    -webkit-animation: before 2s infinite;
            animation: before 2s infinite;
    }
    .loader:after {
    -webkit-animation: after 2s infinite;
            animation: after 2s infinite;
    }

    @-webkit-keyframes before {
    0% {
        width: 0.5em;
        box-shadow: 1em -0.5em rgba(225, 20, 98, 0.75), -1em 0.5em rgba(111, 202, 220, 0.75);
    }
    35% {
        width: 2.5em;
        box-shadow: 0 -0.5em rgba(225, 20, 98, 0.75), 0 0.5em rgba(111, 202, 220, 0.75);
    }
    70% {
        width: 0.5em;
        box-shadow: -1em -0.5em rgba(225, 20, 98, 0.75), 1em 0.5em rgba(111, 202, 220, 0.75);
    }
    100% {
        box-shadow: 1em -0.5em rgba(225, 20, 98, 0.75), -1em 0.5em rgba(111, 202, 220, 0.75);
    }
    }

    @keyframes before {
    0% {
        width: 0.5em;
        box-shadow: 1em -0.5em rgba(225, 20, 98, 0.75), -1em 0.5em rgba(111, 202, 220, 0.75);
    }
    35% {
        width: 2.5em;
        box-shadow: 0 -0.5em rgba(225, 20, 98, 0.75), 0 0.5em rgba(111, 202, 220, 0.75);
    }
    70% {
        width: 0.5em;
        box-shadow: -1em -0.5em rgba(225, 20, 98, 0.75), 1em 0.5em rgba(111, 202, 220, 0.75);
    }
    100% {
        box-shadow: 1em -0.5em rgba(225, 20, 98, 0.75), -1em 0.5em rgba(111, 202, 220, 0.75);
    }
    }
    @-webkit-keyframes after {
    0% {
        height: 0.5em;
        box-shadow: 0.5em 1em rgba(61, 184, 143, 0.75), -0.5em -1em rgba(233, 169, 32, 0.75);
    }
    35% {
        height: 2.5em;
        box-shadow: 0.5em 0 rgba(61, 184, 143, 0.75), -0.5em 0 rgba(233, 169, 32, 0.75);
    }
    70% {
        height: 0.5em;
        box-shadow: 0.5em -1em rgba(61, 184, 143, 0.75), -0.5em 1em rgba(233, 169, 32, 0.75);
    }
    100% {
        box-shadow: 0.5em 1em rgba(61, 184, 143, 0.75), -0.5em -1em rgba(233, 169, 32, 0.75);
    }
    }
    @keyframes after {
    0% {
        height: 0.5em;
        box-shadow: 0.5em 1em rgba(61, 184, 143, 0.75), -0.5em -1em rgba(233, 169, 32, 0.75);
    }
    35% {
        height: 2.5em;
        box-shadow: 0.5em 0 rgba(61, 184, 143, 0.75), -0.5em 0 rgba(233, 169, 32, 0.75);
    }
    70% {
        height: 0.5em;
        box-shadow: 0.5em -1em rgba(61, 184, 143, 0.75), -0.5em 1em rgba(233, 169, 32, 0.75);
    }
    100% {
        box-shadow: 0.5em 1em rgba(61, 184, 143, 0.75), -0.5em -1em rgba(233, 169, 32, 0.75);
    }
    }
    /**
    * Attempt to center the whole thing!
    */
    html,
    body {
    height: 100%;
    }

    .loader {
    position: absolute;
    top: calc(50% - 1.25em);
    left: calc(50% - 1.25em);
    }

#musicLyric{ width: 100%; position: absolute; top:50px; bottom:65px; background:url(/images/detailsBg.jpg); background-size: cover; color:white; overflow-y: hidden; }
#musicLyric ul{ width:100%; text-align:center; color:white; font-size:14px; position:absolute; top:0; left:0; transition:.2s; -webkit-transition:.2s;} 
#musicLyric li{ margin-top:15px;}
#musicLyric li.active{ color:#00ffff;}
#musicAudio{ width:100%; height: 65px; background:url(/images/list_audioBg.png); position: absolute; left:0; bottom: 0; display: -webkit-flex; display: flex; }
#musicAudio .audioPlay{ width:40px;  height:40px; background:url(/images/list_audioPause.png); background-size:cover; margin:10px 20px;}
#musicAudio .audioProgress{ height:2px; background:white; position: relative; -webkit-flex:1 1; flex:1 1; top:31px; margin-right: 20px;}
#musicAudio .audioBar{ width:15px; height:15px; background:#29abe2; border-radius:50%; position:absolute; left:0; top:0; margin:-7px; }
#musicAudio .audioNow{ width:0; height:2px; background:#29abe2; position:absolute; left:0; top:0; }
#musicPic{width: 100%; position: absolute; top:50px; bottom:65px; background:url(/images/detailsBg.jpg); background-size: cover; color:white; overflow-y: hidden; display:-webkit-flex; display:flex}
#musicPic div{width:250px;height:250px;margin:auto;overflow:hidden;border-radius:50%;-webkit-animation:15s linear move infinite;animation:15s linear move infinite}
#musicPic div img{width:100%}

@-webkit-keyframes move{
    0%{
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg)
    }
    100%{
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg)
    }
}

@keyframes move{
    0%{
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg)
    }
    100%{
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg)
    }
}
